  <div>
    <v-container grid-list-xl fluid>
      <header-title title="Consultar resultado final oficial de admisión a las ESFM/UA's MODALIDAD A"></header-title>
      <div v-if="convocatoria.id > 0">
        <v-row>
          <v-col cols="12">
            <v-card>
