                </p>
                <p>
                  El/la postulante puede consultar los resultados de
                  <b>prueba de aptitud</b> (si corresponde),
                  <b>prueba escrita</b> y <b>resultado final</b>, más las
                  observaciones de la modalidad <b>A</b>.
                </p>
                <p>
                  Ingrese su
                  <b>cédula de identidad, complemento y fecha de nacimiento</b>. Tal como ingresó durante la
                  inscripción.
                </p>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
