                        filled
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="1">
                      <v-text-field v-model.trim="postulante.a_postulante.a_persona.complemento" maxlength="2" label="Complemento" :rules="[
                      ]" hint="1G" clearable filled></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <v-menu
                        v-model="menu_date"
                        :close-on-content-click="false"
                        :nudge-right="40"
                        transition="scale-transition"
                        offset-y
                        min-width="290px"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="birthDateFormatted"
                            label="Fecha de nacimiento"
                            append-icon="mdi-calendar"
                            :rules="[(v) => !!v || 'El campo es requerido']"
                            filled
                            readonly
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker
                          v-model="
                            postulante.a_postulante.a_persona.fecha_nacimiento
                          "
                          @input="menu_date = false"
                          locale="es"
                        ></v-date-picker>
                      </v-menu>
                    </v-col>
                    <v-col cols="12" sm="5">
                      <v-btn
                        type="submit"
                        large
                        color="primary"
                        :loading="load_search"
                      >
                        <v-icon>mdi-magnify</v-icon>
                        Buscar
                      </v-btn>
                    </v-col>
                  </v-row>
                </v-form>
                <div v-if="type_state == 3">
                  <h4 class="grey--text">DATOS PERSONALES</h4>
                  <v-row>
                    <v-col cols="12" md="4" class="pt-0">
                      <v-list-item two-line>
                        <v-list-item-content>
                          <v-list-item-title
                            >Apellidos y Nombres</v-list-item-title
                          >
                          <v-list-item-subtitle>
                            {{
                              postulante.a_postulante.a_persona.nombre_completo
                            }}
                          </v-list-item-subtitle>
                        </v-list-item-content>
                      </v-list-item>
                    </v-col>
                    <v-col cols="12" md="4" class="pt-0">
                      <v-list-item two-line>
                        <v-list-item-content>
                          <v-list-item-title>Genero</v-list-item-title>
                          <v-list-item-subtitle>
                            {{
                              postulante.a_postulante.a_persona.genero
                                ? postulante.a_postulante.a_persona.genero
                                    .descripcion
                                : ""
                            }}
                          </v-list-item-subtitle>
                        </v-list-item-content>
                      </v-list-item>
                    </v-col>
                    <v-col cols="12" md="4" class="pt-0">
                      <v-list-item two-line>
                        <v-list-item-content>
                          <v-list-item-title>RESULTADO</v-list-item-title>
                          <v-list-item-subtitle style="font-size: 25px !important;color: #ff0000;">
                            {{ postulante.a_postulante.estado_id==8 || postulante.a_postulante.estado_id==15 ?"ADMITIDO":"NO ADMITIDO" }}
                            
                          </v-list-item-subtitle>
                        </v-list-item-content>
                      </v-list-item>
                    </v-col>
                  </v-row>
                  <h4 class="grey--text mt-5">DATOS DE POSTULACIÓN</h4>
                  <v-row>
                    <v-col cols="12" sm="4" class="pt-0">
                      <v-list-item two-line>
                        <v-list-item-content>
                          <v-list-item-title>ESFM/UA</v-list-item-title>
                          <v-list-item-subtitle>
                            {{ postulante.institucion.nombre }}
                          </v-list-item-subtitle>
                        </v-list-item-content>
                      </v-list-item>
                    </v-col>
                    <v-col cols="12" sm="4" class="pt-0">
                      <v-list-item two-line>
                        <v-list-item-content>
                          <v-list-item-title>Especialidad</v-list-item-title>
                          <v-list-item-subtitle>
                            {{ postulante.especialidad.descripcion }}
                          </v-list-item-subtitle>
                        </v-list-item-content>
                      </v-list-item>
                    </v-col>
                    <v-col cols="12" sm="4" class="pt-0">
                      <v-list-item two-line>
                        <v-list-item-content>
                          <v-list-item-title>Butaca, Sede</v-list-item-title>
                          <v-list-item-subtitle>
                            <span class="indigo--text"
                              >{{ postulante.butaca }},</span
                            >
                            {{ postulante.sede.nombre }}
                          </v-list-item-subtitle>
                        </v-list-item-content>
                      </v-list-item>
                    </v-col>
                  </v-row>
                  <h4 class="grey--text mt-5">RESULTADOS</h4>
                  <v-simple-table class="grid-lines">
                    <template v-slot:default>
                      <thead>
                        <tr>
                          <th class="text-center" colspan="2">
                            Prueba de aptitud
                          </th>
                          <th class="text-center" colspan="2">
                            Prueba escrita
                          </th>
                          <!-- <th class="text-center" rowspan="2">
                            Total sin redendeo
                          </th> -->
                          <th class="text-center" rowspan="2">Total</th>
                          <!-- <th class="text-center" rowspan="2">Admitido</th> -->
                          <th class="text-center" rowspan="2">Obs.</th>
                        </tr>
                        <tr>
                          <th class="text-center">Puntaje</th>
                          <th class="text-center">30%</th>
                          <th class="text-center">Puntaje</th>
                          <th class="text-center">70%</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr class="text-center">
                          <td>
                            {{
                              postulante.nota_aptitud
                                ? postulante.nota_aptitud
                                : "-"
                              }}
                          </td>
                          <td>
                            {{
                              postulante.nota_aptitud
                                ? postulante.porcentaje_aptitud
                                : "-"
                              }}
                          </td>
                          <td>{{ postulante.nota_escrita }}</td>
                          <td>
                            {{
                              postulante.nota_aptitud
                              ? postulante.porcentaje_escrita
                              : "-"
                            }}
                          </td>
                          <!-- <td>
                            {{
                              postulante.porcentaje_aptitud * 1 +
                              postulante.porcentaje_escrita * 1
                            }}
                          </td> -->
                          <td class="text-center">
                            {{ postulante.resultado }}
                          </td>
                          <!-- <td class="text-center">
                            {{ postulante.a_postulante.estado_id==8 || postulante.a_postulante.estado_id==15 ?"SI":"NO" }}
                          </td> -->
                          <td class="text-center">
                            {{ postulante.filtro }}
                          </td>
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                  <h4 class="grey--text mt-5"></h4>
                
                  <a color="#546E7A" target="_blank" download style="font-size: 25px !important"
                    outlined :href="'//dgfmapi.minedu.gob.bo/admision/admitidos/'+ postulante.institucion.id +'.pdf'" >LISTA  OFICIAL  DE ADMITIDOS ESFM UA</a>
                </div>
                <div v-if="type_state === 2">
                  <span class="red--text">
                    No hay registro con
                    <b>Cédula de Identidad y Fecha de Nacimiento</b>
                    ingresada.
                  </span>
                </div>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </div>
      <div v-else>
        <notify-card
          :title="notify_title"
          :load="notify_load"
          process="el proceso de admisión"
        ></notify-card>
      </div>
    </v-container>
  </div>
</template>

<script>
import HeaderTitle from "@/components/HeaderTitle";
import NotifyCard from "@/components/NotifyCard";
import Service from "../services/request";
import { mapActions } from "vuex";

export default {
  name: "admision-consultar-butaca",
  components: {
    HeaderTitle,
    NotifyCard,
  },
  data: () => ({
    notify_title: "Cargando",
    notify_load: true,
    load_search: false,
    menu_date: false,
    type_state: 1,
    convocatoria: {
      id: 0,
      cite: "",
      descripcion: "",
      gestion: "",
    },
    postulante: {
      id:"",
      butaca: "",
      nota_aptitud: "",
      porcentaje_aptitud: "",
      nota_escrita: "",
      porcentaje_escrita: "",
      resultado: "",
      observacion: "",
      filtro: "",
      a_postulante: {
        id:"",
        estado_id: "",
        a_persona: {
          nombre_completo: "",
          nombres: "",
          paterno: "",
          materno: "",
          cedula_dni: "",
          fecha_nacimiento: "",
          complemento:"",
          genero: {},
        },
      },
      institucion: {},
      especialidad: {},
      sede: {},
    },
    admitidos:{

    }
  }),
  computed: {
    birthDateFormatted() {
      if (!this.postulante.a_postulante.a_persona.fecha_nacimiento) return null;

      const [year, month, day] =
        this.postulante.a_postulante.a_persona.fecha_nacimiento.split("-");
      return `${day}/${month}/${year}`;
    },
  },
  created() {
    this.getAnnouncement();
  },
  methods: {
    ...mapActions(["toast"]),
    getAnnouncement() {
      this.notify_load = true;
      Service.svcAdmissionAnnouncement()
        .then((response) => {
          this.convocatoria = response.data;
          this.notify_title = "Notificación";
          this.notify_load = false;

        })
        .catch(() => {
          this.notify_title = "Notificación";
          this.notify_load = false;
        });
    },
    searchPostulante() {
      /* this.postulante = {
        a_postulante: {
          id: "",
          persona: {
            cedula_dni: "",
            paterno: "",
            materno: "",
            nombres: "",
            nombre_completo: "",
            fecha_nacimiento: "",
          },
        },
        institucion: {},
        especialidad: {},
        butaca: "",
        id: "",
      }; */
      this.postulante.a_postulante.a_persona.nombre_completo = "";
      this.postulante.a_postulante.a_persona.genero = {};
      // this.postulante.a_postulante.persona.cedula_dni = "";
      this.postulante.butaca = "";
      this.postulante.sede = "";
      this.postulante.institucion = {};
      this.postulante.especialidad = {};
      this.postulante.nota_aptitud = "-";
      this.postulante.nota_escrita = "-";
      this.postulante.porcentaje_aptitud = "-";
      this.postulante.porcentaje_escrita = "-";
      this.postulante.resultado = "-";
      this.postulante.filtro = "";
      if (this.$refs.formp.validate()) {
        this.load_search = true;
        this.type_state = 1;
        Service.svcCheckResult({
          convocatoria_id: this.convocatoria.id,
          cedula_dni: this.postulante.a_postulante.a_persona.cedula_dni,
          fecha_nacimiento: this.postulante.a_postulante.a_persona.fecha_nacimiento,
          complemento : this.postulante.a_postulante.a_persona.complemento,
        })
          .then(async (response) => {
            this.load_search = false;
            if (response.status == 200) {
              this.type_state = 3;
              this.postulante = response.data;
              

            } else {
              this.type_state = 2;
            }
            
          })
          .catch(() => {
            this.load_search = false;
          });



          
      } else {
        this.type_state = 1;
        this.toast({
          color: "info",
          text: "Ingrese Cédula de identidad y Fecha de nacimiento",
        });
      }
    },
  },
};
</script>
<style scoped>
table thead th {
  background-color: #e0e0e0;
}
